.note_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 300px;
}

.note_heading {
  @extend %font-primary;
  margin: 0;
  padding: 10px;
  text-align: center;
}

.note_content {
  background: #d6d4d4;
  margin: 0;
  @extend %font-content;
  font-size: 1.8rem;
  word-break: break-all;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px;
  //overflow: hidden;
}

.service_info {
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: end;

  & p {
    margin: 0;

  }
}

.note__buttons_block {
  display: flex;
  justify-content: space-between;
}


