.header_container {
  display: flex;
  flex-direction: column;
  background: $color-background;
  @extend %font-primary;
  font-size: 2.5rem;
  padding: 10px;
  border-bottom: black solid 2px;
}

.greeting_user {
  text-align: center;
}

.header__line {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @extend %font-content;
  font-size: 1.3rem;
}